.centerThis {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.selectorButton {
  outline: none;
  color: grey;
  border: none;
  background-color: rgba(0, 0, 0, 0.1);
  cursor: pointer;
  height: 50px;
  width: 170px;
  font-family: poppins;
  font-size: 14;
  font-weight: bold;
  border-radius: 5px;
  transition: all 0.1s ease-in-out;
}
.selectorButton:hover {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

@keyframes Zoom1 {
  0% {
    transform: scale(3, 3);
    filter: blur(30px);
    opacity: 0;
  }
  100% {
    opacity: 1;
    filter: blur(0px);
    transform: scale(1, 1);
  }
}

.Zoom1 {
  animation: Zoom1 0.3s ease-in-out;
}
